* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

#root {
  margin-top: 100px;
}

body {
  background-color: #ebebeb;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type='number'] {
  -moz-appearance: textfield;
}

.centeredSpinner {
  width: 100vw;

  img {
    display: block;
    width: 100px;
    height: 100px;
    margin: 150px auto;
  }
}
